.wsnm-cta, .wsnm-submit-form {
  position: relative;
  display: inline-block;
  margin: 10px 0;
  padding: 10px 15px;
  cursor: pointer;
  font-size: 16px;
  line-height: 16px;
  border-radius: 3px;
}

.wsnm-waiting {
  cursor: not-allowed;
}
.wsnm-waiting:before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.4);
}
.wsnm-waiting .wsnm-spinner {
  width: 16px;
  height: 16px;
  display: block;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.wsnm-waiting .wsnm-spinner:after {
  content: "";
  width: 100%;
  height: 100%;
  border: 2px solid #fff;
  border-top: 2px solid #999;
  border-radius: 50%;
  position: relative;
  display: block;
  animation: wsnm-spin 2s linear infinite;
}

.wsnm-modal {
  position: fixed;
  z-index: 99999999;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.4);
}
.wsnm-modal .wsnm-modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 80%;
  max-width: 700px;
  border-radius: 5px;
}
.wsnm-modal .wsnm-modal-content .wsnm-modal-header {
  position: relative;
  display: block;
  margin-bottom: 20px;
}
.wsnm-modal .wsnm-modal-content .wsnm-modal-header .wsnm-modal-header-text {
  font-weight: 700;
}
.wsnm-modal .wsnm-modal-content .wsnm-modal-header .wsnm-modal-close {
  color: #aaaaaa;
  font-size: 28px;
  line-height: 28px;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  position: absolute;
}
.wsnm-modal .wsnm-modal-content .wsnm-modal-header .wsnm-modal-close:hover, .wsnm-modal .wsnm-modal-content .wsnm-modal-header .wsnm-modal-close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}
.wsnm-modal .wsnm-modal-content .wsnm-modal-row {
  display: flex;
  margin-bottom: 10px;
  position: relative;
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
}
.wsnm-modal .wsnm-modal-content .wsnm-modal-row input {
  width: 100%;
  font-size: 16px;
  line-height: 16px;
  padding: 10px;
  border-radius: 3px;
}
.wsnm-modal .wsnm-modal-content .wsnm-modal-row .wsnm-modal-column {
  width: 48%;
}

#wsnm-ajax-response {
  color: red;
}

@keyframes wsnm-spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}